.profile-outer{
    width: 100%;
    display: flex;
    justify-content: center;
}
.profile-inner{
    width: 90%;
    display: flex;
    justify-content: space-between;
}
.profile-sidebar{
    width: 30%;
}
.profile-sidebar ul{
    list-style: none;
    margin: 0;
    padding: 0;
}
.profile-sidebar ul li{
    display: flex;
    align-items: center;
    margin-top: 27px;
    font-family: 'Darker Grotesque';
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 15px;
    color: #000000;
}
.profile-sidebar ul li span svg{
    font-size: 16px;
}
.profile-sidebar ul li span {
    padding: 10px;
    border-radius: 50%;
    margin-right: 20px;
    background-color: rgba(199, 199, 199, 0.2);
}
.profile-form{
    width: 80%;
}